import { Cliente } from '@/services/api/clientes.service';
import Button from '@/components/ui/Button';
import DeleteIcon from '@/components/common/icons/DeleteIcon';
import EditIcon from '@/components/common/icons/EditIcon';
import ButtonCard from '../ui/button/ButtonCard';
import { useAuth } from '@/hooks/useAuth';

interface ClienteCardProps {
  cliente: Cliente;
  onEdit: () => void;
  onDelete: () => void;
}

const ClienteCard = ({
  cliente,
  onEdit,
  onDelete,
}: ClienteCardProps) => {
  const { user: currentUser } = useAuth();
  return (
    <div 
    // className="rounded-lg bg-white p-4 shadow dark:bg-gray-800">
    className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div className="flex items-start justify-between">
        <div className="space-y-2">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
            {cliente.nome}
          </h3>
          <p className="text-sm text-gray-500 dark:text-gray-400">
            {cliente.cpfCnpj}
          </p>
          <div className="flex flex-wrap gap-4 text-sm text-gray-500 dark:text-gray-400">
            <div>
              <span className="font-medium">Celular:</span> {cliente.celular}
            </div>
            <div>
              <span className="font-medium">Bairro:</span> {cliente.bairro}
            </div>
          </div>
          <p className="text-sm text-gray-500 dark:text-gray-400">
            {cliente.rua}, {cliente.numero}
            {cliente.complemento && ` - ${cliente.complemento}`}
          </p>
        </div>
        <div className={`flex space-x-2`}>
          <ButtonCard
            size="xs"
            variant="secondary"
            onClick={onEdit}
            title={`${currentUser?.role.includes('ADMIN') ? 'Ver cliente' : 'Editar Cliente'}`}
            icon={currentUser?.role.includes('ADMIN') ? 'edit' : 'eye'} />

          <ButtonCard
            size="xs"
            disabled={currentUser?.role.includes('ADMIN') ? false : true}
            variant="secondary"
            onClick={onDelete}
            title="Excluir Cliente"
            icon="delete" />
        </div>
      </div>
    </div>
  );
};

export default ClienteCard;
