"use client";

import React, { useState, ChangeEvent } from 'react';
import axios from 'axios';
import { toast } from 'react-hot-toast';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import Select, { SelectOption } from '@/components/ui/Select';
import DatePicker from '@/components/form/date-picker';
import { Cliente, CreateClienteDTO } from '@/services/api/clientes.service';
import { useAuth } from '@/hooks/useAuth';

type FormErrors = {
  nome?: string;
  cpfCnpj?: string;
  email?: string;
  celular?: string;
  cep?: string;
  rua?: string;
  numero?: string;
  complemento?: string;
  bairro?: string;
  cidade?: string;
  estado?: string;
  nascimento?: string;
};

type ClienteFormProps = {
  cliente?: Cliente;
  onSubmit: (data: CreateClienteDTO) => Promise<void>;
  onCancel: () => void;
};

export default function ClienteForm({ cliente, onSubmit, onCancel }: ClienteFormProps) {
  const { user: currentUser } = useAuth();
  const [formData, setFormData] = useState<CreateClienteDTO>({
    nome: cliente?.nome || '',
    cpfCnpj: cliente?.cpfCnpj || null,
    email: cliente?.email || null,
    celular: cliente?.celular || null,
    cep: cliente?.cep || null,
    rua: cliente?.rua || null,
    numero: cliente?.numero || null,
    complemento: cliente?.complemento || null,
    bairro: cliente?.bairro || null,
    cidade: cliente?.cidade || null,
    estado: cliente?.estado || null,
    extras: cliente?.extras || {},
    nascimento: cliente?.nascimento || new Date().toISOString()
  });

  // Converter extras de objeto para array de campos (suportando formato antigo de array)
  const normalizeExtras = (extras: any): { [key: string]: string } => {
    if (!extras) return {};
    if (Array.isArray(extras)) {
      // Converter array antigo para objeto (retrocompatibilidade)
      const obj: { [key: string]: string } = {};
      for (const extra of extras) {
        if (typeof extra === 'object' && extra !== null) {
          const key = Object.keys(extra)[0];
          if (key) {
            obj[key] = extra[key];
          }
        }
      }
      return obj;
    }
    if (typeof extras === 'object') {
      return extras;
    }
    return {};
  };

  const [extraFields, setExtraFields] = useState<{ key: string; value: string }[]>(
    Object.entries(normalizeExtras(cliente?.extras)).map(([key, value]) => ({
      key,
      value: String(value)
    }))
  );

  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isFetchingCep, setIsFetchingCep] = useState(false);

  // Lista de estados brasileiros
  const estados = [
    { value: 'AC', label: 'Acre' },
    { value: 'AL', label: 'Alagoas' },
    { value: 'AP', label: 'Amapá' },
    { value: 'AM', label: 'Amazonas' },
    { value: 'BA', label: 'Bahia' },
    { value: 'CE', label: 'Ceará' },
    { value: 'DF', label: 'Distrito Federal' },
    { value: 'ES', label: 'Espírito Santo' },
    { value: 'GO', label: 'Goiás' },
    { value: 'MA', label: 'Maranhão' },
    { value: 'MT', label: 'Mato Grosso' },
    { value: 'MS', label: 'Mato Grosso do Sul' },
    { value: 'MG', label: 'Minas Gerais' },
    { value: 'PA', label: 'Pará' },
    { value: 'PB', label: 'Paraíba' },
    { value: 'PR', label: 'Paraná' },
    { value: 'PE', label: 'Pernambuco' },
    { value: 'PI', label: 'Piauí' },
    { value: 'RJ', label: 'Rio de Janeiro' },
    { value: 'RN', label: 'Rio Grande do Norte' },
    { value: 'RS', label: 'Rio Grande do Sul' },
    { value: 'RO', label: 'Rondônia' },
    { value: 'RR', label: 'Roraima' },
    { value: 'SC', label: 'Santa Catarina' },
    { value: 'SP', label: 'São Paulo' },
    { value: 'SE', label: 'Sergipe' },
    { value: 'TO', label: 'Tocantins' },
  ];

  const validateForm = () => {
    const newErrors: FormErrors = {};
    const required = ['nome'];

    required.forEach(field => {
      if (!formData[field as keyof typeof formData]) {
        newErrors[field as keyof FormErrors] = `${field.charAt(0).toUpperCase() + field.slice(1)} é obrigatório`;
      }
    });

    if (formData.email && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = 'Email inválido';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    console.log('Form Data:', formData);
    if (!validateForm()) return;
    console.log('Extra Fields:', extraFields);

    // Converte extraFields para objeto simples (formato esperado pela API)
    const formattedExtras = Object.fromEntries(
      extraFields
        .filter(field => field.key && field.value) // Remove campos vazios
        .map(field => [field.key, field.value])
    );

    const submitData = {
      ...formData,
      extras: formattedExtras
    };

    setIsSubmitting(true);
    try {
       await onSubmit(submitData);
    } catch (error: any) {
      const erros = error.response?.data?.details || [];
      erros.forEach((err: any) => {
        const field = err.field.replace('body.', '');
        setErrors(prev => ({ ...prev, [field]: err.message }));
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className={`space-y-6  ${currentUser?.role.includes('ADMIN') ? '' : 'pointer-events-none opacity-50'}`}>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="md:col-span-2">
          <Label htmlFor="nome">Nome</Label>
          <Input
            id="nome"
            name="nome"
            value={formData.nome}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, nome: e.target.value })}
            error={errors.nome}
          />
        </div>

        <div>
          <Label htmlFor="cpfCnpj">CPF ou CNPJ</Label>
          <Input
            id="cpfCnpj"
            name="cpfCnpj"
            value={formData.cpfCnpj || ''}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, cpfCnpj: e.target.value || null })}
            error={errors.cpfCnpj}
          />
        </div>

        <div>
          <Label htmlFor="nascimento">Data de Nascimento</Label>
          {/* <Input
            id="nascimento"
            name="nascimento"
            type="date"
            value={formData.nascimento?.split('T')[0]}
            onChange={(e: ChangeEvent<HTMLInputElement>) => 
              setFormData({ 
                ...formData, 
                nascimento: new Date(e.target.value).toISOString() || null
              })
            }
            error={errors.nascimento}
          /> */}

          <DatePicker
            id="data"
            onChange={(selectedDates: Date[]) => {
              if (selectedDates[0]) {
                setFormData({
                  ...formData,
                  nascimento: selectedDates[0].toISOString()
                });
              }
            }}
            defaultDate={new Date(formData.nascimento || new Date().toISOString())}
          />
          {errors.nascimento && (
            <p className="mt-1 text-sm text-red-600">{errors.nascimento}</p>
          )}
        </div>

        <div>
          <Label htmlFor="email">Email</Label>
          <Input
            id="email"
            name="email"
            type="email"
            value={formData.email || ''}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, email: e.target.value || null })}
            error={errors.email}
          />
        </div>

        <div>
          <Label htmlFor="celular">celular</Label>
          <Input
            id="celular"
            name="celular"
            value={formData.celular || ''}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, celular: e.target.value || null })}
            error={errors.celular}
          />
        </div>

        <div>
          <Label htmlFor="cep">CEP</Label>
          <Input
            id="cep"
            name="cep"
            value={formData.cep || ''}
            onChange={async (e: ChangeEvent<HTMLInputElement>) => {
              const value = e.target.value.replace(/\D/g, '');
              setFormData({ ...formData, cep: value });
              if (value.length === 8) {
                setIsFetchingCep(true);
                try {
                  const { data } = await axios.get(`https://brasilapi.com.br/api/cep/v1/${value}`);
                  setFormData(prev => ({
                    ...prev,
                    cep: data.cep,
                    rua: data.street || null,
                    bairro: data.neighborhood || null,
                    cidade: data.city || null,
                    estado: data.state || null,
                  }));
                } catch (err) {
                  toast.error('CEP não encontrado');
                } finally {
                  setIsFetchingCep(false);
                }
              }
            }}
            error={errors.cep}
            disabled={isFetchingCep}
          />
        </div>

        <div>
          <Label htmlFor="rua">Rua</Label>
          <Input
            id="rua"
            name="rua"
            value={formData.rua || ''}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, rua: e.target.value || null })}
            error={errors.rua}
          />
        </div>

        <div>
          <Label htmlFor="numero">Número</Label>
          <Input
            id="numero"
            name="numero"
            value={formData.numero || ''}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, numero: e.target.value || null })}
            error={errors.numero}
          />
        </div>

        <div>
          <Label htmlFor="complemento">Complemento</Label>
          <Input
            id="complemento"
            name="complemento"
            value={formData.complemento || ''}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, complemento: e.target.value || null })}
          />
        </div>

        <div>
          <Label htmlFor="bairro">Bairro</Label>
          <Input
            id="bairro"
            name="bairro"
            value={formData.bairro || ''}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, bairro: e.target.value || null })}
            error={errors.bairro}
          />
        </div>

        <div>
          <Label htmlFor="cidade">Cidade</Label>
          <Input
            id="cidade"
            name="cidade"
            value={formData.cidade || ''}
            onChange={(e: ChangeEvent<HTMLInputElement>) => setFormData({ ...formData, cidade: e.target.value || null })}
            error={errors.cidade}
          />
        </div>

        <div>
          <Label htmlFor="estado">Estado</Label>
          <Select
            id="estado"
            name="estado"
            value={estados.find(estado => estado.value === formData.estado)}
            options={estados}
            onChange={(option) => setFormData({ ...formData, estado: (option as SelectOption)?.value || null })}
            isSearchable
            placeholder="Selecione o estado"
            error={errors.estado}
            className="w-full"
          />
        </div>

        <div className="md:col-span-2">
          <Label>Campos Extras</Label>
          {extraFields.map((field, index) => (
            <div key={index} className="flex gap-4 mb-4">
              <Input
                placeholder="Chave"
                value={field.key}
                onChange={(e) => {
                  const newFields = [...extraFields];
                  newFields[index].key = e.target.value;
                  setExtraFields(newFields);
                }}
              />
              <Input
                placeholder="Valor"
                value={field.value}
                onChange={(e) => {
                  const newFields = [...extraFields];
                  newFields[index].value = e.target.value;
                  setExtraFields(newFields);
                }}
              />
              <Button
                type="button"
                variant="danger"
                onClick={() => {
                  const newFields = extraFields.filter((_, i) => i !== index);
                  setExtraFields(newFields);
                }}
              >
                Remover
              </Button>
            </div>
          ))}
          <Button
            type="button"
            variant="secondary"
            onClick={() => {
              setExtraFields([...extraFields, { key: '', value: '' }]);
            }}
          >
            Adicionar Campo Extra
          </Button>
        </div>
      </div>

      <div className={`flex justify-end space-x-4 pt-4 ${currentUser?.role.includes('ADMIN') ? '' : 'hidden'}`}>
        <Button
          variant="secondary"
          onClick={onCancel}
          type="button"
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          loading={isSubmitting}
        >
          {cliente ? 'Salvar Alterações' : 'Criar Cliente'}
        </Button>
      </div>
    </form>
  );
}
