"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { services } from '@/services/api';
import { useAuth } from '@/hooks/useAuth';
import toast from 'react-hot-toast';
import Button from '@/components/ui/Button';
import SearchInput from '@/components/form/input/SearchInput';
import { Modal } from '@/components/ui/modal';
import ClienteCard from '@/components/clientes/ClienteCard';
import ClienteForm from '@/components/clientes/ClienteForm';
import { Cliente, CreateClienteRequest } from '@/services/api/clientes.service';

export default function ClientesPage() {
  const { user: currentUser } = useAuth();
  const [clientes, setClientes] = useState<Cliente[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedCliente, setSelectedCliente] = useState<Cliente | undefined>();
  const itemsPerPage = 9; // 3x3 grid

  const loadClientes = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.cliente.list({
        page,
        limit: itemsPerPage,
        ...(searchTerm && { search: searchTerm })
      });
      setClientes(response.data);
      setTotalPages(Math.ceil(response.pagination.total / itemsPerPage));
    } catch (error) {
      toast.error('Erro ao carregar clientes');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, itemsPerPage]);

  useEffect(() => {
    loadClientes();
  }, [loadClientes]);

  const handleEdit = (cliente: Cliente) => {
    setSelectedCliente(cliente);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir este cliente?')) return;

    try {
      await services.cliente.delete(id);
      toast.success('Cliente excluído com sucesso');
      loadClientes();
    } catch (error) {
      toast.error('Erro ao excluir cliente');
    }
  };

  const handleSave = async (data: CreateClienteRequest) => {
    try {
      if (selectedCliente) {
        const cliente = await services.cliente.update(selectedCliente.id, {
          ...data,
          complemento: data.complemento,
          nascimento: data.nascimento,
          extras: data.extras
        });
        console.log('cliente atualizado:', cliente);
        toast.success('Cliente atualizado com sucesso');
      } else {
        const cliente = await services.cliente.create({
          ...data,
          complemento: data.complemento,
          nascimento: data.nascimento,
          extras: data.extras
        });
        console.log('cliente criado:', cliente);
        toast.success('Cliente criado com sucesso');
      }
      setIsModalOpen(false);
      setSelectedCliente(undefined);
      loadClientes();
    } catch (error) {
      console.error('Erro ao salvar cliente:', error);
      toast.error('Erro ao salvar cliente');
      throw error;
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Clientes
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie seus clientes
            </p>
          </div>
          <Button 
            variant="primary"
            disabled={currentUser?.role.includes('ADMIN') ? false : true}
            onClick={() => {
              setSelectedCliente(undefined);
              setIsModalOpen(true);
            }}
          >
            Novo Cliente
          </Button>
        </div>

        <div className="mt-4 w-full max-w-sm">
          <SearchInput
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            placeholder="Buscar clientes..."
          />
        </div>
      </div>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-500"></div>
        </div>
      ) : (
        <>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {clientes.map((cliente) => (
              <ClienteCard
                key={cliente.id}
                cliente={cliente}
                onEdit={() => handleEdit(cliente)}
                onDelete={() => handleDelete(cliente.id)}
              />
            ))}
          </div>

          {clientes.length === 0 && (
            <div className="text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhum cliente encontrado
              </p>
            </div>
          )}

          {totalPages > 1 && (
            <div className="mt-6 flex justify-center">
              <nav className="flex items-center gap-2">
                <Button
                  variant="secondary"
                  onClick={() => setPage((p) => Math.max(1, p - 1))}
                  disabled={page === 1}
                >
                  Anterior
                </Button>
                <span className="text-sm text-gray-700 dark:text-gray-300">
                  Página {page} de {totalPages}
                </span>
                <Button
                  variant="secondary"
                  onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
                  disabled={page === totalPages}
                >
                  Próxima
                </Button>
              </nav>
            </div>
          )}
        </>
      )}

      <Modal 
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedCliente(undefined);
        }} 
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14 mb-6">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {currentUser?.role.includes('ADMIN') ? (selectedCliente ? 'Editar Cliente' : 'Novo Cliente') : 'Visualizar Cliente'}
            </h4>
            <p className={` text-sm text-gray-500 dark:text-gray-400 lg:mb-7 ${currentUser?.role.includes('ADMIN') ? '' : 'hidden'}`}>
              {selectedCliente 
                ? 'Atualize as informações do cliente.' 
                : 'Preencha os dados para criar um novo cliente.'
              }
            </p>
          </div>
        <ClienteForm
          cliente={selectedCliente}
          onSubmit={handleSave}
          onCancel={() => {
            setIsModalOpen(false);
            setSelectedCliente(undefined);
          }}
        />
        </div>
      </Modal>
    </div>
  );
}
